import Vue from 'vue'
import Vuex from 'vuex'
import http from '@/api/http'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    notice: 0,
    cargoList: [],
    seaPort: [],
    riverPort: [],
    toHome:1,
  },
  getters: {
    getNotice(state) {
      return state.notice
    },
    getCargoList(state) {
      return state.cargoList
    },
    getSeaPort(state) {
      return state.seaPort
    },
    getRiverPort(state) {
      return state.riverPort
    },
    toHome(state){
      return state.toHome
    },
  },
  mutations: {
    getNotice(state, payload) {
      state.cargoList = payload
    },
    getCargoList(state, payload) {
      state.cargoList = payload
    },
    getSeaPort(state, payload) {
      state.seaPort = payload
    },
    getRiverPort(state, payload) {
      state.riverPort = payload
    },
    toHome(state,payload){
      state.toHome = payload
    },
  },
  actions: {
    getNoticeFn({
      commit
    }, data) {
      commit('getNotice', data)
    },
    goToHome(context, data) {
      context.commit('toHome', data)
    },
    // 货种列表
    async getCargoListFn({
      commit
    }, data) {
      let res = await http.getCargosList()
      if (!res.data.status && res.data.result instanceof Array) {
        commit('getCargoList', res.data.result)
      }
    },
    // 海运港口
    async getSeaPortFn({
      commit
    }, data) {
      let res = await http.getSeaPort()

      if (!res.data.status && res.data.result instanceof Array) {
        let result = res.data.result
        let finResult = []
        for(let i in result){
              finResult.push({
                label:result[i]['province'],
                value:result[i]['code'],
                children:[],
              })
          for(let j in result[i]['children']){
                finResult[i]['children'].push({
                  label:result[i]['children'][j]['port'],
                  value:result[i]['children'][j]['code'],
                })
            }

        }
        commit('getSeaPort',finResult)
      }

    },
    // 江运列表
    async getRiverPortFn({
      commit
    }, data) {
      let res = await http.getRiverPort()
      if (!res.data.status && res.data.result instanceof Array) {
        let result = res.data.result
        let finResult = []
        for(let i in result){
              finResult.push({
                label:result[i]['province'],
                value:result[i]['code'],
                children:[],
              })
          for(let j in result[i]['children']){
                finResult[i]['children'].push({
                  label:result[i]['children'][j]['port'],
                  value:result[i]['children'][j]['code'],
                })
            }

        }
        commit('getRiverPort', finResult)
      }
    },
  }
})



