import * as apis from './api'
// HEAD
import {
  $http
} from './axios'

export default {
  // 登录
  login(data) {
    return $http('post', apis.ApiLogin, data)
  },
  // 货主列表
  huozhuList(data) {
    return $http('post', apis.ApiHuoZhuList, data)
  },
  // 货主详情
  huozhuDetail(data) {
    return $http('post', apis.ApiHuoZhuDetail, data)
  },
  updatehzInfo(data) {
    return $http('post', apis.ApiupdateHz, data)
  },
  // 查询货主
  selectCargoOwnerInfo(data) {
    return $http('post', apis.ApiselectCargoOwnerInfo, data)
  },
  // 船东列表
  cdList(data) {
    return $http('post', apis.ApicdList, data)
  },
  // 船东详情
  cdDetail(data) {
    return $http('post', apis.ApicdDetail, data)
  },
  // 修改船东信息
  updatecd(data) {
    return $http('post', apis.ApiupdateCd, data)
  },
  // 查询船东
  selectShipOwnerInfo(data) {
    return $http('post', apis.ApiselectShipOwnerInfo, data)
  },

  // 船舶列表
  shipList(data) {
    return $http('post', apis.ApishipList, data)
  },
  // 船舶详情
  shipDetail(data) {
    return $http('post', apis.ApishipDetail, data)
  },
  // 船舶详情
  updateShip(data) {
    return $http('post', apis.ApiupdateShip, data)
  },
  // 查询船舶
  selectShipInfo(data) {
    return $http('post', apis.ApiselectShipInfo, data)
  },
  // 港口列表
  portList(data) {
    return $http('post', apis.ApiportList, data)
  },
  // 港口详情
  portDetail(data) {
    return $http('post', apis.ApiportDetail, data)
  },
  // 修改港口信息
  updatePort(data) {
    return $http('post', apis.ApiupdatePort, data)
  },
  // 查询港口
  selectPortInfo(data) {
    return $http('post', apis.ApiselectPortInfo, data)
  },
  // 添加货种
  addCargo(data) {
    return $http('post', apis.ApiaddCargo, data)
  },
  // 货种详情
  getCargoInfo(data) {
    return $http('get', apis.ApigetCargoInfoByCargo, data)
  },
  // 修改货种详情
  updateCargoInfoByCargo(data) {
    return $http('post', apis.ApiupdateCargoInfoByCargo, data)
  },
  // 根据货种添加货种信息
  addCargoInfoByCargo(data) {
    return $http('post', apis.ApiaddCargoInfoByCargo, data)
  },
  // 电厂详情
  getPowerPlantInfo(data) {
    return $http('get', apis.ApigetPowerPlantInfo, data)
  },
  // 添加电厂
  addPowerPlantInfo(data) {
    return $http('post', apis.ApiaddPowerPlant, data)
  },
  // 添加修改电厂日耗详情
  addPowerPlantDailyInfo(data) {
    return $http('post', apis.ApiaddPowerPlantDailyInfo, data)
  },
  // 删除电厂日耗详情
  deletePowerPlantDailyInfo(data) {
    return $http('post', apis.ApideletePowerPlantDailyInfo, data)
  },
  // 删除电厂详情
  deletePowerPlantInfo(data) {
    return $http('post', apis.ApideletePowerPlantInfo, data)
  },
  // 平台油价详情
  getPlatformFuelPriceInfo() {
    return $http('get', apis.ApigetPlatformFuelPriceInfo)
  },
  // 供应商油价详情
  getFuelComapanyPriceInfo() {
    return $http('get', apis.ApigetFuelComapanyPriceInfo)
  },
  // 修改油价详情
  updateFuelComapanyPrice(data) {
    return $http('post', apis.ApiupdateFuelComapanyPrice, data)
  },
  // 保存修改平台报价
  addPlatformPrice(data) {
    return $http('post', apis.ApiaddPlatformPrice, data)
  },
  // 添加供应商
  addFuelComapany(data) {
    return $http('post', apis.ApiaddFuelComapany, data)
  },
  // 删除供应商
  deleteFuelComapany(data) {
    return $http('post', apis.ApideleteFuelComapany, data)
  },
  // 删除供应商油价种类
  deleteFuelComapanyPrice(data) {
    return $http('post', apis.ApideleteFuelComapanyPrice, data)
  },
  // 删除供应商油价种类
  addFuelComapanyPrice(data) {
    return $http('post', apis.ApiaddFuelComapanyPrice, data)
  },
  // 获取货种列表
  getCargosList(data) {
    return $http('get', apis.ApigetCargoList, data)
  },
  // 获取海运港口列表
  getSeaPort(data) {
    return $http('get', apis.ApigetSeaPort, data)
  },
  // 获取江运港口列表
  getRiverPort(data) {
    return $http('get', apis.ApigetRiverPort, data)
  },
  // 运力匹配江运
  riverShipListByInfo(data) {
    return $http('post', apis.ApiRiverShipListByInfo, data)
  },
  // 运力匹配海运
  seaShipListByInfo(data) {
    return $http('post', apis.ApiSeaShipListByInfo, data)
  },
  // 运力匹配 船舶详情 历史合作
  shipHistoryOrder(data) {
    return $http('post', apis.ApiShipHistoryOrder, data)
  },
  //运力匹配 船舶详情 历史航线
  shipHistoryVoyage(data) {
    return $http('post', apis.ApiShipHistoryVoyage, data)
  },
  //运力匹配 船舶详情
  shipInfoAndLostPosition(data) {
    return $http('post', apis.ApiShipInfoAndLostPosition, data)
  },
  //添加、修改货种
  AddCargo(data) {
    return $http('post', apis.ApiAddCargo, data)
  },
  //删除货种种类
  updateCargoType(data) {
    return $http('post', apis.ApiUpdateCargoType, data)
  },
  //获取所有货种
  getAllCargoType() {
    return $http('get', apis.ApiGetAllCargoType)
  },

  //获取船舶五天轨迹
  getShipHistory(data) {
    return $http('post', apis.ApigetShipHistory, data)
  },

}



