<template>
  <div class="box">
    <div class="logo-box">
      <div> <img src="http://fygw.oss-cn-shanghai.aliyuncs.com/white_logo.png" alt="" class="logo" @click="toHome"> </div>
      <div class="titles">海易通数据管理中心</div>
    </div>
    <div class="userInfo">
      <Menu mode="vertical">
        <Submenu name="1">
          <template slot="title">
            <Icon type="ios-contact"></Icon>
            {{user}}

          </template>
          <Menu-item name="3-1" class="menu-items" @click.native="loginOut">
            <span>退出</span>
          </Menu-item>
        </Submenu>
      </Menu>
    </div>
  </div>
</template>
<script>
  import {  mapActions } from "vuex";
   export default  {
       data(){
          return {
             user:"",
             num:2
          }
       },
       methods:{
         ...mapActions(['goToHome']),
         loginOut(){
              localStorage.removeItem("id");
              localStorage.removeItem("token");
              localStorage.removeItem("staff_name");
              localStorage.removeItem('name')
              this.$router.push({
                path: "/login"
              });
         },
         toHome(){
              localStorage.setItem('name','1-1')
              this.$router.replace({
                path: "/"
              });
              this.goToHome(this.num++)
         }
       },
       mounted(){
         this.user  =localStorage.staff_name
       }
   }
</script>
<style  scoped lang='scss'>
$left:left;
$right:right;
.box {
  box-sizing: border-box;
  position: fixed;
  display: block;
  width: 100%;
  height: 60px;
  z-index: 20;
  top: 0;
  left: 0;
  box-shadow: 0 2px 1px 1px hsla(0, 0%, 39%, 0.1);
  background-color: #393d49;
  min-width: 1200px;
}
.userInfo{
  float: $right;
  height: 60px;
}
.logo-box{
  float: left;
  height: 60px;
  width: 50%;
  color: #fff;
  font-size: 18px;
  display: flex;
  line-height: 60px;
}
.logo{
  width: 150px;
  margin: 5px 30px;
  cursor: pointer;
}

.ivu-menu {
  height: 60px;
  line-height: 40px;
   background-color:#393D49;
   color: #fff;
}
.ivu-menu-submenu{
  padding: 0px 40px;
}
.ivu-menu-vertical .ivu-menu-item{
   padding: 0;
}
.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu){
  line-height: 45px;
  height: 45px;
  border: none;
  // background: none;
}
.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu):hover{
  background-color: lightgrey;
  color: #fff;
}
</style>



