<template>
  <div id="app" :class="isLogin?'active':''">
    <navbar v-if='isLogin'></navbar>
    <sidebar v-if='isLogin'></sidebar>
    <router-view />
  </div>
</template>

<script>
import sidebar from "@/components/sidebar";
import navbar from "@/components/navbar";
export default {
  name: "App",
  data(){
    return {
      isLogin:false
    }
  },
  components: {
    sidebar,
    navbar
  },
   watch:{
     $route(to,from){
       if(to.path === '/login'){
         this.isLogin = false
       }else{
         this.isLogin = true
       }
    }

},
  mounted(){
      window.addEventListener('beforeunload', e => {
            localStorage.removeItem('searchInfo')
      });
  },
};
</script>

<style lang='scss'>
$fontSize: 16px;
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  min-width: 1200px;
}
.active{
    padding: 60px 0 0 240px;
}
.card {
  position: relative;
  left: 5px;
  right: 0;
  overflow: auto;
  z-index: 1;
  transition: left 0.3s;
  height: 100%;
}
.box-container{
    margin-top: 40px;
}
.pages{
  margin-top: 30px;
}

.row{
  line-height: 35px;
}

</style>



