
<template>
  <div class="sidebar">
      <Menu :active-name="name" :open-names="['1']" @on-select='select'>
         <Submenu :name="sub.name" v-for="sub in sidebar" :key='sub.name'>
            <template slot="title">
                <Icon :type="sub.Icon" />
               {{sub.Submenu}}
            </template>
          <router-link :to="subItem.path" v-for="subItem in sub.MenuItem" :key='subItem.name'> <MenuItem :name="subItem.name" > {{subItem.title}}</MenuItem></router-link>
        </Submenu>
    </Menu>
  </div>
</template>
<script>
import sidebar from '@/config/sidebar'
import { mapGetters, mapActions } from "vuex";
export default {
    data(){
        return {
            sidebar:'',
            name:'1-1',
        }
    },
  computed:{
      ...mapGetters(['toHome'])
    },
   watch:{
      toHome(){
            this.select('1-1')
      }
    },
   methods:{
      select(name){
          this.name =name
          localStorage.setItem('name',name)
      }
    },
   mounted(){
       this.sidebar = sidebar
       this.name = localStorage.name ? localStorage.name : this.name
   }
}

</script>
<style  scoped lang='scss'>
$color:#fff;
$bgColor:#393D49;
.sidebar{
    position: fixed;
    top: 60px;
    bottom: 0;
    left: 0;
    z-index: 2;
    background-color:$bgColor;
    color: $color;
    overflow: auto;
}
 .ivu-menu .ivu-menu-light .ivu-menu-vertical {
    color: #fff !important;
}
.ivu-menu-light{
  color: #fff;
   background-color:#393D49;

}
.ivu-menu-item{
 color: #fff;
}
.ivu-menu-item a{
 color: #fff;
}
.ivu-menu-item:hover a{
 color: #2d8cf0;
}

.ivu-menu-item-group-title{
  color: #fff;
}
.ivu-menu-item-group{
  color: #fff;

}
.ivu-menu-submenu .ivu-menu-submenu-has-parent-submenu{

  color: #fff;
}
 >>> .ivu-menu-item-selected a{
    color: #2d8cf0;
}

>>> .ivu-menu-vertical.ivu-menu-light:after {
 background-color:#393D49;
}
</style>



