import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
/**
 * 关于路由懒加载的说明
 * resolve
 *  这里如果 require [] 数组里 如果是 按常量引入的时候 webpack 编译的时候会有warning
 *  如果是字符串的方式 webpack 编译的时候就不会有 warning
 * @auther peipei
 */
export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: resolve => require(['@/pages/login.vue'], resolve),
    },
    {
      path: '/',
      name: 'yunli',
      component: resolve => require(['@/pages/yunlipipei/yunli.vue'], resolve),
    },
    {
      path: '/yunli/yunliship',
      name: 'yunliship',
      component: resolve => require(['@/pages/yunlipipei/yunliShipDetail.vue'], resolve),
    },
    {
      path: '/hzCenter',
      name: 'hzCenter',
      component: resolve => require(['@/pages/huozhuCenter/huozhuList.vue'], resolve),

    },
    {
      path: '/hzCenter/hzlist',
      name: 'hzlist',
      component: resolve => require(['@/pages/huozhuCenter/huozhuList.vue'], resolve),
    }, {
      path: '/hzCenter/updatehz',
      name: 'updatehz',
      component: resolve => require(['../pages/huozhuCenter/upHuoZhu.vue'], resolve),
    },
    {
      path: '/hzCenter/hzdetail',
      name: 'hzdetail',
      component: resolve => require(['@/pages/huozhuCenter/huozhuDetail.vue'], resolve),
    },
    {
      path: '/cdCenter',
      name: 'cdCenter',
      component: resolve => require(['@/pages/chuandongCenter/cdList.vue'], resolve),


    },
    {
      path: '/cdCenter/cdlist',
      name: 'cdlist',
      component: resolve => require(['@/pages/chuandongCenter/cdList.vue'], resolve),
    }, {
      path: '/cdCenter/updatecd',
      name: 'updatecd',
      component: resolve => require(['@/pages/chuandongCenter/updateCd.vue'], resolve),
    },
    {
      path: '/cdCenter/cddetail',
      name: 'cddetail',
      component: resolve => require(['@/pages/chuandongCenter/cdDetail.vue'], resolve),
    },

    {
      path: '/shipCenter',
      name: 'shipCenter',
      component: resolve => require(['@/pages/shipCenter/shipList.vue'], resolve),


    },
    {
      path: '/shipCenter/shiplist',
      name: 'shiplist',
      component: resolve => require(['@/pages/shipCenter/shipList.vue'], resolve),
    }, {
      path: '/shipCenter/updateship',
      name: 'updateship',
      component: resolve => require(['@/pages/shipCenter/updateShip.vue'], resolve),
    },
    {
      path: '/shipCenter/shipdetail',
      name: 'shipdetail',
      component: resolve => require(['@/pages/shipCenter/shipDetail.vue'], resolve),
    },
    {
      path: '/portCenter',
      name: 'portCenter',
      component: resolve => require(['@/pages/portCenter/portList.vue'], resolve),


    },
    {
      path: '/portCenter/portlist',
      name: 'portlist',
      component: resolve => require(['@/pages/portCenter/portList.vue'], resolve),
    }, {
      path: '/portCenter/updateport',
      name: 'updateport',
      component: resolve => require(['@/pages/portCenter/updatePort.vue'], resolve),
    },
    {
      path: '/portCenter/portdetail',
      name: 'portdetail',
      component: resolve => require(['@/pages/portCenter/portDetail.vue'], resolve),
    },

    {
      path: '/goodsprice',
      name: 'goodsprice',
      component: resolve => require(['@/pages/marketCenter/goodsPrice.vue'], resolve),
    },
    {
      path: '/goodssupply',
      name: 'goodssupply',
      component: resolve => require(['@/pages/marketCenter/goodsSupply.vue'], resolve),
    },
    {
      path: '/oilprice',
      name: 'oilprice',
      component: resolve => require(['@/pages/marketCenter/oilPrice.vue'], resolve),
    },
    {
      path: '/addCargo',
      name: 'addCargo',
      component: resolve => require(['@/pages/marketCenter/addCargo.vue'], resolve),
    },
    {
      path: '*',
      name: 'notfund',
      component: resolve => require(['@/pages/notfund.vue'], resolve),
    },
  ]
})



